<template>
  <div class="category-list">
    <scroll-view
        ref="scroll"
        class="scroll-wrapper"
    >
      <sub-category :subCategory="subCategory"/>
      <tab-control :titles="['综合', '新品', '销量']"
                   @tabClick="tabClick"/>
      <goods-list :goodsList="goodsInfo"/>
    </scroll-view>
  </div>
</template>

<script>
import SubCategory from "@/views/category/childComps/SubCategory";
import ScrollView from "@/components/scroll/ScrollView";
import TabControl from "@/components/tabControl/TabControl";
import GoodsList from "@/components/goodslist/GoodsList";
import {getCategoryDetail} from "@/network/category";

export default {
  name: "CategoryList",
  components: {GoodsList, TabControl, SubCategory, ScrollView},
  props: {
    subCategory: {
      type: Array,
      default() {
        return []
      }
    },
    miniWallkey: {
      type: String,
      default() {
        return ""
      }
    }
  },
  data() {
    return {
      curIdx: 0,
      curType: 'pop',
      goodsInfo: []
    }
  },
  methods: {
    tabClick(index) {
      console.log(index)
      if (this.curIdx === index) {
        return
      }
      this.curIdx = index
      switch (index) {
        case 0:
          this.getTabData("pop")
          this.curType = 'pop'
          break;
        case 1:
          this.getTabData("sell")
          this.curType = 'sell'
          break;
        case 2:
          this.getTabData("new")
          this.curType = 'new'
          break;
        default:
          break
      }
    },
    getTabData() {
      getCategoryDetail(this.miniWallkey, this.curType).then(response => {
        this.goodsInfo = response
        console.log(this.goodsInfo)
        this.$refs.scroll.refresh()
      })
    }
  },
  computed: {
    // showType() {
    //   return this.goodsInfo[this.curType].list;
    // },
  },
  watch: {
    miniWallkey: function (newQuestion, oldQuestion) {
      console.log("监听到变化了:" + newQuestion + " / " + oldQuestion)
      this.getTabData()
    }
  },
  mounted() {

  }
}
</script>

<style lang="less" scoped>
.category-list {
  height: calc(100% - 44px - 50px);

  .scroll-wrapper {
    overflow: hidden;
    height: 100%;
  }
}
</style>
