<template>
  <div class="category-slider">
    <scroll-view
        ref="scroll"
        class="scroll-wrapper"
    >
      <div class="item">
        <span :class="{active:curIndex === index}" @click="itemClick(index)" v-for="(item,index) in categories"
              :key="index">{{ item.title }}</span>
      </div>
    </scroll-view>
  </div>
</template>

<script>
import ScrollView from "@/components/scroll/ScrollView";

export default {
  name: "SliderBar",
  props: {
    categories: {
      type: Array,
      default() {
        return []
      }
    },
  },
  components: {ScrollView},
  data() {
    return {
      curIndex: 0
    }
  },
  methods: {
    itemClick(index) {
      this.curIndex = index
      this.$emit("categoryItemClick", index)
    }
  }
}
</script>

<style lang="less" scoped>
.category-slider {
  width: 120px;
  background-color: rgba(245, 245, 245, .5);
  height: calc(100% - 44px - 50px);

  .scroll-wrapper {
    overflow: hidden;
    height: 100%;

    .item {
      display: flex;
      text-align: center;
      align-items: center;
      flex-direction: column;

      span {
        padding: 20px 15px;
        width: 100%;
        height: 100%;
        font-weight: 500;
        font-size: 16px;
      }

      .active {
        color: var(--color-high-text);
        border-left: 2px solid var(--color-high-text);
        background-color: white;
      }
    }
  }
}
</style>
