<template>
  <div class="category">
    <nav-bar class="title">
      <div slot="center">商品分类</div>
    </nav-bar>
    <div class="content-list">
      <slider-bar :categories="categories"
                  @categoryItemClick="itemClick"/>
      <category-list :subCategory="subCategory" :miniWallkey="miniWallkey"/>
    </div>
  </div>
</template>

<script>
import {getCategoryData, getSubCategory} from "@/network/category";

import NavBar from "@/components/navbar/NavBar";

import SliderBar from "@/views/category/childComps/SliderBar";
import CategoryList from "@/views/category/childComps/CategoryList";

export default {
  components: {CategoryList, SliderBar, NavBar},
  data() {
    return {
      categories: [],
      subCategory: [],
      curIndex: 0,
      maitKey: "",
      miniWallkey: "",
    }
  },
  methods: {
    itemClick(index) {
      this.maitKey = this.categories[index].maitKey
      this.miniWallkey = this.categories[index].miniWallkey
      this.getcgeDetail()
    },
    getcgeDetail() {
      getSubCategory(this.maitKey).then(response => {
        this.subCategory = response.data.list
      })
    }
  },
  created() {
    getCategoryData().then(response => {
      if (response) {
        this.categories = response.data.category.list
        this.maitKey = this.categories && this.categories[0].maitKey
        this.miniWallkey = this.categories && this.categories[0].miniWallkey
        this.getcgeDetail()
      }
    })
  }
}
</script>

<style lang="less" scoped>
.category {
  height: 100vh;
  overflow: hidden;
  background-color: #fff;

  .title {
    background-color: var(--color-tint);
    color: white;
    font-size: 18px;
  }

  .content-list {
    display: flex;
    //height: calc(100% - 44px);
    height: 100%;

    :last-child {
      flex: 1;
    }
  }

}
</style>
